@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Ubuntu", sans-serif;
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
}
.Top {
  height: 5em;
  width: 100%;

  border-bottom: 1px solid #d0d3d4;
}
form {
  border: none;
  height: 5em;

  display: flex;
  justify-content: space-around;
  align-items: center;
}
form img {
  width: 3em;
}
input:focus {
  outline: none;
}
input[type="text"] {
  font-size: 1.4rem;
  font-family: "Ubuntu", sans-serif;
}
button:focus {
  outline: none;
}
.search-bar {
  width: 60%;
  height: 1.5em;
  border-radius: 10px;
  border: none;
  border: 1px solid black;
}
.search-button {
  background: none;
  border: none;
}
.search-button img {
  width: 3em;
}
.main {
  width: 90%;
  max-width: 95vw;
  margin: 0 auto;
  display: block;
}
.main h1 {
  font-size: 2.2rem;
}
.main-title {
  display: flex;
  justify-content: space-between;
}
.main-container {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-row-gap: 8em;
  justify-items: center;
  text-align: center;
}
.main-container h1 {
  font-size: 1.4rem;
}
.main-container img {
  width: 250px;
}
.bot {
  margin-top: 6em;
  width: 100%;
  height: 16em;
  background-color: #01020b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #d0d3d4;
}
.bot img {
  width: 5.5em;
}
.bot ul {
  list-style: none;
}
.bot li {
  line-height: 2em;
}
.bot li:hover {
  color: white;
}
.bot h1 {
  color: orange;
}
.bot h1:hover {
  color: orangered;
}
@media screen and (max-width: 400px) {
  .App {
    font-size: 0.6rem;
  }
}

.container-page2 {
  font-family: "Ubuntu", sans-serif;
  background: url(/static/media/backgr1.ecc9143b.png) center no-repeat;
  background-size: cover;
  background-attachment: local;
  width: 100%;
  height: 100vh;
  min-width: 320px;
  margin: 0 auto;
  overflow-x: hidden;
}
.container-page2 .top-page2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1em;
}
.top-page2 .logo {
  margin-left: 2em;
}
.top-page2 .social-icon {
  margin-right: 2em;
}
.top-page2 img {
  width: 2em;
}
.top-page2 .home-button a {
  color: black;
  font-size: 2rem;
  text-transform: uppercase;
  text-decoration: none;
}
.title-page2 {
  margin: 3em 0 0 6em;
  width: 100%;
}
.main-page2 {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.main-page2 .main-content {
  width: 50%;
  font-size: 1.2rem;
  line-height: 2em;
  margin-left: 5em;
  padding-bottom: 10em;
}
.main-page2 .image-container {
  width: 40%;
  margin-left: 8em;
}
.image-container img {
  width: 19em;
}

